import React, { Component } from 'react'

import Push from './Push'
import Pull from './Pull'
import BizGen from './BizGen'

export default class PushBtn extends Component {
  constructor(props) {
    super(props)
  }
  render () {
    const { bizAction = 'PUSH' } = this.props
    const compMap = {
      PUSH: Push,
      PULL: Pull,
      GEN_VOUCHER: BizGen
    }
    const BizBtn = compMap[bizAction]
    return <BizBtn {...this.props}></BizBtn>
  }
}