import React from 'react'
import classnames from 'classnames'
const { Label } = require('mdf-metaui-web').basic;

if (process.env.__CLIENT__ === true) {
  require('./index.less')
}

export default class ApproveOpinion extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      value: '',
      visible: !props.bHidden,
      cShowCaption: props.cShowCaption
    }
  }

  componentDidMount () {
    if (this.props.model) {
      this.props.model.addListener(this);
    }
  }

  componentWillUnmount () {
    if (this.props.model) {
      this.props.model.removeListener(this);
    }
  }


  render () {
    const { classname, err, visible, value } = this.state;
    const { className } = this.props;
    const opinionContent = value ? value.replace(/\n/g, '<br/>').replace(/\$PRINTASHTML\$/g, '') : ''

    if (!visible) return null;

    let cShowCaption = this.props.cShowCaption;
    if (cShowCaption && cShowCaption != this.state.cShowCaption) cShowCaption = this.state.cShowCaption;
    const title = <label>{cShowCaption}</label>
    const _class = classnames('has-feedback', `basic-input-${this.state.type}`, classname, err, className);
    const relatedControl = <div className="approve-opinion" dangerouslySetInnerHTML={{ __html: opinionContent }}></div>
    return (
      <div className={_class}>
        {(cShowCaption && !this.props.hideTitle) ? <Label control={relatedControl} title={title} /> : relatedControl}
      </div>
    );
  }
}
