import React, { Component } from 'react';
import classnames from 'classnames'
import { Button } from 'yonui-ys';
import { getNidAndUitype } from '@mdf/metaui-web/lib/components/basic/util';
import { PUSH_CODE_FIELD } from './const'

export default class PullBtn extends Component {
  constructor (props) {
    super(props);
    this.state = {
      cShowCaption: props.cShowCaption,
      hide: false,
      visible: true
    };
    this.nidAnduitypeContainer = getNidAndUitype(props)
  }

  componentDidMount () {
    if (this.props.model)
      this.props.model.addListener(this);
  }

  componentDidUpdate () {
    if (this.props.model)
      this.props.model.addListener(this);
  }

  componentWillUnmount () {
    if (this.props.model)
      this.props.model.removeListener(this);
  }

  // componentDidMount () {
  //   const viewmodel = this.props.model?.getParent();
  //   viewmodel && viewmodel.on('afterBizflowbatchpush', function (data) {
  //     viewmodel.biz.do('abandon', viewmodel, {})
  //   })
  // }

  handleClick = () => {
    const viewmodel = this.props.model?.getParent();
    const vmParams = viewmodel.getParams()
    const { custMap = {}, targetBillNo, showConvertedBill } = vmParams?.query || {}
    const { sourceDomain, targetDomain } = custMap
    const gridModel = viewmodel.getGridModel()
    const selectRows = gridModel.getSelectedRows();

    if (!selectRows.length) {
      cb.utils.alert('请选择有效数据', 'warning')
      return
    }
    const billNo = viewmodel.getParams()?.billNo

    const actionName = showConvertedBill ? 'batchpush' : 'bizflowbatchpush'
    const data = gridModel.getNecessaryDataForRows(selectRows)
    const cSvcUrl = showConvertedBill
      ? `bizflow/batchPush?code=${targetDomain}&groupCode=${billNo}2${targetBillNo}&targetBillNo=${targetBillNo}&domain=${targetDomain}`
      : `bizflow/batchPush?domainKey=${sourceDomain}`
    const params = {
      cHttpMethod: 'POST',
      cSvcUrl,
      extendData: custMap,
      carryParams: {
        custMap
      },
      data: JSON.stringify(data)
    }
    if (showConvertedBill && targetDomain === 'developplatform'){
      params.cParameter = JSON.stringify({
        pushCodeField: PUSH_CODE_FIELD
      })
    }
    viewmodel.biz.do(actionName, viewmodel, params)
  }

  render () {
    const { hide, disabled, menuVisiable, cShowCaption } = this.state;
    const style = this.state.visible ? {} : { display: 'none' };
    return (
      <Button className="m-l-8" onClick={this.handleClick}>{cShowCaption}</Button>
    )
  }
}
