export function getNidAndUitype (props) {
  if (!props || props.bHidden) return {}
  let {nid = '', uitype = '', nidPriority = ''} = props;
  if (!nid && !uitype) return {};
  if (nidPriority) {
    nid = nidPriority;
    return {nid, uitype}
  }
  return {nid, uitype}
}

export function fireKeyEvent (el, evtType, keyCode) {
  let evtObj;
  if (document.createEvent) {
    if (window.KeyEvent) { // firefox 浏览器下模拟事件
      evtObj = document.createEvent('KeyEvents');
      evtObj.initKeyEvent(evtType, true, true, window, true, false, false, false, keyCode, 0);
    } else { // chrome 浏览器下模拟事件
      evtObj = document.createEvent('UIEvents');
      evtObj.initUIEvent(evtType, true, true, window, 1);

      delete evtObj.keyCode;
      if (typeof evtObj.keyCode === 'undefined') { // 为了模拟keycode
        Object.defineProperty(evtObj, 'keyCode', { value: keyCode });
      } else {
        evtObj.key = String.fromCharCode(keyCode);
      }

      // if (typeof evtObj.ctrlKey === 'undefined') { // 为了模拟ctrl键
      //   Object.defineProperty(evtObj, 'ctrlKey', { value: true });
      // } else {
      //   evtObj.ctrlKey = true;
      // }
    }
    el.dispatchEvent(evtObj);
  } else if (document.createEventObject) { // IE 浏览器下模拟事件
    evtObj = document.createEventObject();
    evtObj.keyCode = keyCode
    el.fireEvent('on' + evtType, evtObj);
  }
}

export function isTimePicker (target) {
  return cb.dom(target).hasClass('u-time-picker-input');
}

export function isDatePicker (target) {
  return cb.dom(target).hasClass('datepicker-input-group');
}

export function isSelect (target) {
  return cb.dom(target).hasClass('u-select-selection');
}

export function isListRefer (target) {
  return cb.dom(target).hasClass('uretail-treerefer-selection');
}

export function isTagRefer (target) {
  return cb.dom(target).hasClass('TagList');
}

export function isRadioGroup (target) {
  return cb.dom(target).hasClass('u-radio-group');
}

export function isCheckboxGroup (target) {
  return cb.dom(target).hasClass('s-checkgroup-wrapper');
}

export function isTextarea (target) {
  return target && (target.tagName === 'TEXTAREA');
}

