export const decodeValue = (value, formatReg, hiddenChart = '*', replaceChart = '#') => {
  if (!formatReg && !Object.prototype.toString.call(formatReg) !== '[object String]') {
    return value;
  }
  const isHiddenChartEqualsToReplaceChart = hiddenChart === replaceChart
  formatReg = formatReg.split('');
  value = String(value).split('');
  let newValue = [];
  for (let index = 0; index < formatReg.length; index++) {
    const element = formatReg[index];

    if (element === replaceChart) {
      const cell = value.shift();
      if (!cell) break;
      if (isHiddenChartEqualsToReplaceChart) {
        newValue.push(hiddenChart)
      } else {
        newValue.push(cell);
      }
    } else if (element === hiddenChart) {
      const cell = value.shift();
      if (!cell) break;
      newValue.push(hiddenChart);
    } else {
      newValue.push(element);
    }
  }
  if (value.length > 0) {
    newValue = newValue.concat(value)
  }

  return newValue.join('');
}

export const GlobalEvent = {
  un (event, func) {
    if (!cb.events) return;
    if (typeof cb.events.un === 'function') {
      cb.events.un(event, func);
    }
  },
  register (event, func, flag) {
    if (!cb.events) return;
    if (typeof cb.events.on === 'function') {
      cb.events.on(event, func);
    }
    // 自动触发一次
    flag && func();
  },
  emit (event, ...args) {
    if (!cb.events) return;
    if (cb.events.hasEvent(event) && typeof cb.events.execute === 'function') {
      cb.events.execute(event, ...args);
    }
  }
}

let diworkContentOffset = 0
export const getDiworkContentOffset = () => {
  if (diworkContentOffset) {
    return diworkContentOffset
  }
  const diworkContentDom = document.querySelector('.diwork-content-fixed');
  // 非工作台页面，直接返回；（兼容零售端）
  if (!diworkContentDom) return;
  // 8px 的内嵌阴影
  diworkContentOffset = diworkContentDom.offsetTop + 8;
  return diworkContentOffset
}

/**
 * 字符串格式 json 添加属性
 * @params target json 格式字符串
 * @params object Object
 * @return string
 */
export const jsonStrAddItem = (target, object) => {
  let config = {}
  try {
    config = JSON.parse(target);
  } catch (e) {
    config = {}
  }
  return JSON.stringify({ ...config, ...object });
}
