import React, { Component } from 'react';
import classnames from 'classnames'
import { Menu, Dropdown, Button } from 'yonui-ys';
import getNidAndUitype from '../../utils/getNidAndUitype';

const MenuItem = Menu.Item;

export default class PushBtn extends Component {
  constructor (props) {
    super(props);
    this.state = {
      cShowCaption: props.cShowCaption,
      hide: false,
      menuVisiable: false,
      dataSource: [],
      visible: true
    };
    this.nidAnduitypeContainer = getNidAndUitype(props)
  }

  componentDidMount () {
    if (this.props.model)
      this.props.model.addListener(this);
  }

  componentDidUpdate () {
    if (this.props.model)
      this.props.model.addListener(this);
  }

  componentWillUnmount () {
    if (this.props.model)
      this.props.model.removeListener(this);
  }

  onVisibleChange = (visible) => {
    this.setState({ hide: !visible });
    if (this.props.onVisibleChange)
      this.props.onVisibleChange(visible);
  }

  _getSearchParams = () => {
    const viewmodel = this.props.model?.getParent();
    const { billNo} = viewmodel.getParams();
    const params = {
      billnum: billNo.replace('List', ''),
      custMap: { ruleType: '6' }
    }
    return params
  }

  getMenuItems = () => {
    const params = this._getSearchParams();
    const self = this;
    const viewmodel = this.props.model?.getParent();
    const proxy = cb.rest.DynamicProxy.create({
      ensure: {
        url: '/bizflow/searchRule',
        method: 'POST',
        options: {
          ruleType: '6',
          domainKey: viewmodel?.getDomainKey()
        }
      }
    });
    proxy.ensure(params, function (_err, result) {
      if (result.length == '0') {
        cb.utils.alert('无可用操作，请先配置业务流', 'warning')
        self.setState({
          dataSource: [],
          menuVisiable: false
        })
      } else {
        self.setState({
          dataSource: result,
          menuVisiable: true
        })
      }
    });
  }

  onMenuVisibleChange = (newVisible) => {
    if (!newVisible) {
      this.setState({
        menuVisiable: newVisible
      });
    }
  }

  handleMenuItemClick = menuItem => {
    const viewmodel = this.props.model?.getParent();
    const { targetType, originType, pullType, businessFlowId, tenantId, id: ruleId, targetDomain, originDomain: sourceDomain, showConvertedBill } = menuItem
    const [ , targetBillNo ] = `${targetType}`.split('.')
    const [ , sourceBillNo ] = `${originType}`.split('.')
    this.setState({
      menuVisiable: false
    })
    viewmodel.biz.do('pullx', viewmodel, {
      cmdParameter: JSON.stringify({
        // 选单页单号
        billnumber: pullType,
        // 下游单据billno A 拉 B 则为A
        targetBillNo,
        showConvertedBill,
        custMap: {
          // 上游单据billno A 拉 B则为B
          billnum: sourceBillNo,
          businessFlowId,
          tenantId,
          ruleId,
          sourceDomain,
          targetDomain
        }
      })
    })
  }

  loop = data => data.map((item) => {
    return <MenuItem key={item.id} bussinessFlowId={item.bussinessFlowId} onClick={(e) => this.handleMenuItemClick(item)}>{item.name}</MenuItem>;
  });

  // 首次渲染显示button，点击时先判断当前有没有选中的单据 请求下拉内容
  // 点击下拉菜单时，将菜单相关参数传递
  render () {
    const { hide, disabled, menuVisiable, cShowCaption } = this.state;
    const menu = (
      <Menu>
        {this.loop(this.state.dataSource || [])}
      </Menu>
    );
    const style = this.state.visible ? {} : { display: 'none' };
    return (
      <Dropdown overlay={menu} trigger={['click']} visible={menuVisiable} onVisibleChange={this.onMenuVisibleChange}>
        <Button style={style} {...this.nidAnduitypeContainer} className={classnames('meta-workflow-button', { hide: !!hide }, this.props.className)} disabled={disabled} onClick={this.getMenuItems}>
          {cShowCaption}
        </Button>
      </Dropdown>
    );
  }
}
