import React, { Component } from 'react';
import classnames from 'classnames'
import { Menu, Dropdown, Button } from 'yonui-ys';
import getNidAndUitype from '../../utils/getNidAndUitype';
import { PUSH_CODE_FIELD } from './const'

const MenuItem = Menu.Item;

export default class PushBtn extends Component {
  constructor (props) {
    super(props);
    this.state = {
      cShowCaption: props.cShowCaption,
      value: this.props.value,
      hide: false,
      menuVisiable: false,
      dataSource: [],
      visible: true
    };
    this.nidAnduitypeContainer = getNidAndUitype(props)
  }

  componentDidMount () {
    if (this.props.model)
      this.props.model.addListener(this);
  }

  componentDidUpdate () {
    if (this.props.model)
      this.props.model.addListener(this);
  }

  componentWillUnmount () {
    if (this.props.model)
      this.props.model.removeListener(this);
  }

  onVisibleChange = (visible) => {
    this.setState({ hide: !visible });
    if (this.props.onVisibleChange)
      this.props.onVisibleChange(visible);
  }

  /**
   * @description 根据当前单据页面类型返回对应的参数，列表页获取表格单据数据，
   *              卡片页返回当前页面数据
   * @memberof WorkFlowBtn
   */
  _getSearchParams = () => {
    const viewmodel = this.props.model?.getParent();
    const { billNo} = viewmodel.getParams();
    const isList = this._isListPage();
    const params = {
      billnum: billNo
    };
    if (isList) {
      const gridModel = viewmodel.getGridModel()
      const selectRows = gridModel.getSelectedRows();
      if (!selectRows.length) {
        cb.utils.alert('请选择有效数据', 'warning')
        return
      }
      params.data = JSON.stringify(selectRows);
    } else {
      params.data = JSON.stringify(viewmodel.collectData(true));
    }
    return params;
  }

  _isListPage = () => {
    const viewmodel = this.props.model?.getParent();
    const {billType = ''} = viewmodel.getParams();
    const listType = ['voucherlist', 'editvoucherlist', 'archivelist']
    const isList = listType.includes(billType.toLowerCase())
    return isList
  }

  getMenuItems = async () => {
    const self = this;
    const params = this._getSearchParams();
    const viewmodel = this.props.model?.getParent();
    if (params) {
      const proxy = cb.rest.DynamicProxy.create({
        ensure: {
          url: '/bizflow/searchRule',
          method: 'POST',
          options: {
            domainKey: viewmodel?.getDomainKey()
          }
        }
      });
      proxy.ensure(params, function (_err, result) {
        if (result.length == '0') {
          cb.utils.alert('无可用操作，请先配置业务流', 'warning')
          self.setState({
            dataSource: [],
            menuVisiable: false
          })
        } else {
          self.setState({
            dataSource: result,
            menuVisiable: true
          })
        }
      });
    }
  }

  onMenuVisibleChange = (newVisible) => {
    if (!newVisible) {
      this.setState({
        menuVisiable: newVisible
      });
    }
  }

  handleMenuItemClick = (menuItem) => {
    const viewmodel = this.props.model?.getParent();
    let actionName, cSvcUrl, data;
    const { targetType, targetDomain } = menuItem
    const [ , targetBillNo ] = `${targetType}`.split('.')
    const billNo = viewmodel.getParams()?.billNo

    const gridModel = viewmodel.getGridModel()
    const sourceDomain = viewmodel?.getDomainKey()
    let { showConvertedBill } = menuItem
    
    if (this._isListPage()) {
      const selectDatas = gridModel.getSelectedRows();
      data = gridModel.getNecessaryDataForRows(selectDatas)
      // 列表转单推
      if (showConvertedBill) {
        actionName = 'batchpush';
        cSvcUrl = `bizflow/batchPush?code=${targetDomain}&groupCode=${billNo}2${targetBillNo}&targetBillNo=${targetBillNo}&domain=${targetDomain}`;
      // 列表直接推
      } else {
        actionName = 'bizflowbatchpush';
        cSvcUrl = `bizflow/batchPush?domainKey=${sourceDomain}`;
      }
    } else {
      data = [viewmodel.collectData(true)]
      // 卡片转单推
      if (showConvertedBill) {
        actionName = 'push';
        cSvcUrl = `bizflow/batchPush?code=${targetDomain}&groupCode=${billNo}2${targetBillNo}&targetBillNo=${targetBillNo}&domain=${targetDomain}`;
      } else {
      // 卡片直接推
        actionName = 'bizflowpush';
        cSvcUrl = `bizflow/push?domainKey=${sourceDomain}`;
      }
    }
    const extendData = {
      businessFlowId: menuItem.businessFlowId,
      tenantId: menuItem.tenantId,
      ruleId: menuItem.id,
      billnum: billNo,
      sourceDomain,
      targetDomain
    }
    const params = {
      cHttpMethod: 'POST',
      cSvcUrl,
      extendData,
      carryParams: {
        custMap: extendData,
      },
      data: JSON.stringify(data)
    }
    if (showConvertedBill && targetDomain === 'developplatform'){
      params.cParameter = JSON.stringify({
        pushCodeField: PUSH_CODE_FIELD
      })
    }
    this.setState({
      menuVisiable: false
    })
    viewmodel.biz.do(actionName, viewmodel, params)
  }

  loop = data => data.map((item) => {
    return <MenuItem key={item.id} bussinessFlowId={item.bussinessFlowId} onClick={(e) => this.handleMenuItemClick(item)}>{item.name}</MenuItem>;
  });

  // 首次渲染显示button，点击时先判断当前有没有选中的单据 请求下拉内容
  // 点击下拉菜单时，将菜单相关参数传递
  render () {
    const { hide, disabled, menuVisiable, cShowCaption } = this.state;
    const menu = (
      <Menu>
        {this.loop(this.state.dataSource || [])}
      </Menu>
    );
    const style = this.state.visible ? {} : { display: 'none' };
    return (
      <Dropdown overlay={menu} trigger={['click']} visible={menuVisiable} onVisibleChange={this.onMenuVisibleChange}>
        <Button style={style} {...this.nidAnduitypeContainer} className={classnames('meta-workflow-button', { hide: !!hide }, this.props.className)} disabled={disabled} onClick={this.getMenuItems}>
          {cShowCaption}
        </Button>
      </Dropdown>
    );
  }
}
